.ce-element--empty {
  outline: 2px solid #079397;
  height: 3em;
  border-radius: 3px;
}

span.ce-element {
  display: block;
}

.ct-widget .ct-tag {
  background-color: #df376e !important;
}
.ct-widget .ct-tag:after {
  border-left-color: #df376e !important;
}
.ct-widget .ct-tag:hover {
  background-color: #079397 !important;
}
.ct-widget .ct-tag:hover:after {
  border-left-color: #079397 !important;
}
.ct-widget .ct-tool--subheading3:before {
  content: "H";
  font-size: 15px !important;
}
.ct-widget .ct-tool--subheading4:before {
  content: "H";
  font-size: 10px !important;
}
.ct-widget .ct-tool--subheading5:before {
  content: "H";
  font-size: 8px !important;
}
.ct-widget .ct-tool.ct-tool--disabled:before {
  color: rgba(51, 51, 51, 0.5);
}
.ct-widget .ct-tool:before {
  color: #333;
}
.ct-widget .ct-tool:after {
  background: #fff;
  color: #000;
  font-size: 14px;
  bottom: 30px;
  word-break: inherit;
  left: 50%;
  -webkit-hyphens: inherit;
  transform: translateX(-50%);
  width: auto;
}
.ct-widget .ct-tool:hover:after {
  opacity: 0.8;
  visibility: visible;
  transition-property: opacity;
  transition-duration: 0.3s;
  transition-timing-function: ease-in;
  transition-delay: 1s;
}
.ct-widget.ct-toolbox {
  background: rgba(255, 255, 255, 0.6) !important;
  -webkit-backdrop-filter: blur(0.2rem);
  backdrop-filter: blur(0.2rem);
  border: 1px solid rgba(255, 255, 255, 0.2);
  box-shadow: -0.25rem -0.25rem 0.5rem rgba(255, 255, 255, 0.07), 0.25rem 0.25rem 0.5rem rgba(0, 0, 0, 0.12), -0.75rem -0.75rem 1.75rem rgba(255, 255, 255, 0.07), 0.75rem 0.75rem 1.75rem rgba(0, 0, 0, 0.12), inset 8rem 8rem 8rem rgba(0, 0, 0, 0.05), inset -8rem -8rem 8rem rgba(255, 255, 255, 0.05);
  width: auto;
  max-width: 248px;
  transition: background 0.5s;
}
.ct-widget.ct-toolbox:hover {
  background: rgba(255, 255, 255, 0.8) !important;
}
.ct-widget.ct-toolbox .ct-grip {
  border-bottom: solid 1px rgba(51, 51, 51, 0.4);
  width: calc(100% + 16px);
  margin-left: -8px;
  background-image: url(./images/move.svg);
  background-repeat: no-repeat;
  background-position: center center;
  opacity: 0.8;
  transition: opacity 0.3s;
  height: 35px;
  margin-top: -10px;
}
.ct-widget.ct-toolbox .ct-grip:hover {
  opacity: 1;
}
.ct-widget.ct-toolbox .ct-grip .ct-grip__bump {
  display: none;
}
.ct-widget.ct-ignition {
  bottom: 12%;
  right: 5%;
  left: inherit;
  top: inherit;
  display: flex;
  flex-direction: row-reverse;
  width: 170px;
  transform: translateY(-100%) !important;
}
.ct-widget .ct-ignition__button {
  transition: box-shadow 0.5s, opacity 0.5s, background-color 0.5s;
  box-shadow: 0 5px 10px rgba(0, 0, 0, 0.2);
  opacity: 0.9;
}
.ct-widget .ct-ignition__button:hover {
  opacity: 1;
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.3);
}
.ct-widget .ct-ignition__button:before {
  content: "";
  width: 100%;
  height: 100%;
  position: absolute;
  left: 0;
  top: 0;
  background-position: center center;
  background-repeat: no-repeat !important;
  background-size: 40px 40px;
}
.ct-widget .ct-ignition__button[class*=edit] {
  background-color: #079397;
}
.ct-widget .ct-ignition__button[class*=edit]:before {
  transform: none !important;
  background-image: url(./images/pencil.svg);
}
.ct-widget .ct-ignition__button[class*=edit]:hover {
  background-color: #078a8d;
}
.ct-widget .ct-ignition__button[class*=confirm] {
  background-color: #079397;
}
.ct-widget .ct-ignition__button[class*=confirm]:before {
  background-image: url(./images/check.svg);
}
.ct-widget .ct-ignition__button[class*=confirm]:hover {
  background-color: #078a8d;
}
.ct-widget .ct-ignition__button[class*=cancel] {
  background-color: #df376e;
}
.ct-widget .ct-ignition__button[class*=cancel]:before {
  background-image: url(./images/close.svg);
}
.ct-widget .ct-ignition__button[class*=cancel]:hover {
  background-color: #de2e68;
}
.ct-widget.ct-anchored-dialog {
  margin-top: -58px !important;
}

main {
  overflow: hidden;
}

.ce-element--focused, .ce-element--over {
  background-color: transparent;
  position: relative;
}
.ce-element--focused:before, .ce-element--focused:after, .ce-element--over:before, .ce-element--over:after {
  opacity: 1 !important;
}

