.revision-selector {
  position: absolute;
  margin-top: -30px;
  z-index: 100;
}
.revision-selector .revision-list {
  background: rgba(255, 255, 255, 0.9);
  border: solid 1px #999;
  padding: 10px;
}
.revision-selector .revision-list li {
  color: #222 !important;
  text-align: left;
}
.revision-selector li.uk-active {
  font-weight: bold;
}

.ct-tool--clock::before {
  content: "\e94e";
}

.ct-tool--logout::before {
  content: "\ea7d";
}